
.PaginationContainerOuter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 5px;
}

.PaginationContainerInner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #2b2d42;
    width: 95%;
}

.ItemsPerPageContainer {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
}

.ItemsPerPageLabel {
    margin: 0px 10px 0px 0px;
    font-size: 0.8em;
    white-space: nowrap;
}

.ItemsPerPageButton {
    width: fit-content;
    min-width: 30px;
    background-color: rgb(237, 242, 244);
    border: 1px solid rgb(141, 153, 174);    
    margin: 1px;
    font-size: 0.8em;
}

.ItemsPerPageButton:hover {
    background-color: rgb(242, 246, 248);
    cursor: pointer;
}

.ItemsPerPageButton:focus {
    outline: none;
}

.ItemsPerPageButton:active {
    border: 1px solid black;
    background-color: rgb(223, 227, 235);

}

.PageNumberContainer {
    display: flex;
    flex-direction: row;
}

.PageNumberButton {
    font-size: 0.8em;
    background-color: rgb(237, 242, 244);
    border: 1px solid rgb(141, 153, 174);
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.PageNumberButton:hover {
    background-color: rgb(242, 246, 248);
    cursor: pointer;
}

.PageNumberButton:focus {
    outline: none;
}

.PageNumberButton:active {
    border: 1px solid black;
    background-color: rgb(223, 227, 235);
}

.PageNumberInput {
    width: 30px;
    text-align: center;
    font-size: 0.8em;
}

::placeholder {
    color: black;
    opacity: 1;
}