.ListItemContainer {
  border: 1px double rgb(141, 153, 174);
  margin-bottom: 5px;
  background-color: #f1f7fa;
}

.Completed {
  opacity: 0.5;
}

.DateContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 5px 20px 5px 0px;
}

.DateAdded {
  margin: 0px;
  font-size: 0.9em;
  font-style: italic;
  font-weight: 200;
  letter-spacing: 1px;
  color: rgb(141, 153, 174);
}

.TodoTextContainer {
  position: relative;
  border: 1px dotted rgb(141, 153, 174);
  margin: 10px 20px 10px 20px;
  background-color: #fff;
  border-radius: 4px;
}

.TodoTextContainer:hover .TextEditButton {
  display: inline;
}

.TodoText {
  padding-left: 10px;
  padding-right: 10px;
  white-space: pre-line;
}

.TextEditButton {
  position: absolute;
  right: 0;
  top: 0;
  display: none;
  margin: 0px 5px 0px 0px;
  font-style: italic;
  font-size: 0.8em;
  font-weight: 300;
  color: rgb(141, 153, 174);
}

.TextEditButton:hover {
  text-decoration: underline;
  cursor: pointer;
}

.EditTextTextarea {
  width: 95%;
  height: 100%;
  resize: none;
  border: none;
}

.EditTextTextarea:focus {
  outline: none;
}

.EditTextButtonsContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  color: red;
}

.SubmitTextButton {
  white-space: nowrap;
  padding: 0px 40px 0px 40px;
}

.CancelTextButton {
  padding: 0% 10px 0% 10px;
}

.BottomBar {
  border-top: 1px dashed rgb(201, 201, 201);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
}

.ButtonBar {
  flex-basis: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ExpiresContainer {
  flex-basis: 70%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}

.EditExpiryText {
  visibility: hidden;
  margin: 0px 0px 0px 5px;
  font-size: 0.7em;
  font-style: italic;
  color: rgb(141, 153, 174);
}

.ExpiresContainer:hover .EditExpiryText {
  visibility: visible;
}

.ExpiresContainer:hover .EditExpiryText:hover {
  text-decoration: underline;
  cursor: pointer;
  color: rgb(43, 45, 66);
}

.ExpiresText {
  font-size: 0.9em;
  margin: 0px;
  font-style: italic;
  font-weight: 200;
  letter-spacing: 1px;
  color: rgb(141, 153, 174);
  padding-left: 20px;
}

.ExpiredText {
  color: red;
  font-style: normal;
}

.Expired {
  color: red;
  font-weight: 800;
  font-size: 1.3em;
}

.ExpiryEditContainer {
  padding-top: 5px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.ExpiryEditText {
  margin: 0px;
  font-size: 0.8em;
}

.ExpiryPickerEditContainer {
  padding-left: 5px;
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.ExpiryPickerEdit {
  margin-left: 5px;
}

.ExpiryRemoveCancel {
  text-align: center;
  display: flex;
  justify-content: flex-end;
  padding-right: 25px;
  align-items: center;
  margin: 0px;
  font-size: 0.8em;
  flex: 1;
}

.RemoveExpiryButton {
  margin: 0px;
}

.RemoveExpiryButton:hover {
  text-decoration: underline;
  cursor: pointer;
  color: rgb(217, 4, 41);
}

.CancelExpiryEditButton {
  margin: 0px;
  margin-left: 10px;
}

.CancelExpiryEditButton:hover {
  text-decoration: underline;
  cursor: pointer;
  color: rgb(217, 4, 41);
}

.ExpiryEditSubmit {
  flex: 1;
}

.ImportantIcon {
  font-size: 1.6em;
  color: rgb(255, 228, 94);
  text-shadow: 0px 0px 2px rgb(240, 240, 240);
}

.ImportantIcon:hover {
  color: rgb(255, 196, 0);
  cursor: pointer;
}

.DeleteIcon {
  color: rgb(217, 4, 41);
}

.DeleteIcon:hover {
  color: rgb(255, 0, 30);
  cursor: pointer;
}

.CompletedIcon {
  color: rgb(43, 45, 66);
}

.CompletedIcon:hover {
  color: grey;
  cursor: pointer;
}

@media only screen and (max-width: 900px) {
  .ExpiryRemoveCancel {
    justify-content: center;
    padding: 0px;
  }
}

@media only screen and (max-width: 600px) {
  .ExpiresText {
    font-size: 0.8em;
  }

  .SubmitTextButton {
    white-space: nowrap;
    padding: 0px 10px 0px 10px;
  }
}

.Important {
  border: 1px solid rgb(255, 196, 0);
  background: linear-gradient(45deg, #ffe45e 1%,#faea92 10%,#f6f1c6 20%,#f1f7fa 50%,#f6f1c6 80%,#faea92 90%,#ffe45e 100%);
}

